/** @jsxRuntime classic */
/** @jsx h */
import { getPersonalizationFilters } from '@algolia/recommend-core';
import { useEffect, useState } from 'preact/hooks';
export const usePersonalizationFilters = ({
  apiKey,
  appId,
  userToken,
  region
}) => {
  const [status, setStatus] = useState('loading');
  const [filters, setPersonalizationFilters] = useState([]);
  useEffect(() => {
    if (userToken && region) {
      setStatus('loading');
      getPersonalizationFilters({
        apiKey,
        appId,
        region,
        userToken
      }).then(result => {
        setPersonalizationFilters(result);
        setStatus('idle');
      }).catch(() => {
        setStatus('stalled');
      });
    } else {
      setStatus('idle');
      setPersonalizationFilters([]);
    }
  }, [apiKey, appId, userToken, region, setStatus]);
  return {
    filterStatus: status,
    personalizationFilters: filters
  };
};